 <template>
    <section class="nav-about container">
      <div 
        class="btn-box"
        :class="{ active: currentPage === index }"
        v-for="(btn, index) in buttonsInfo"
        :key="index"
        @click="getBtnIndex(index)"
      >
        <button>{{ btn.title }}</button>
      </div>
    </section>
</template>


<script>
export default {
  name: 'Nav-about',
  props: {
    currentPage: {
      type: Number,
      default: () => 0
    }
  },
  data() {
    return {
      buttonsInfo: [
        {
          title: 'History of the company'
        },
        {
          title: 'Our Team'
        },
        {
          title: 'Our goal and mission'
        },
        {
          title: 'Certificates'
        },
      ]
    }
  },
  methods: {
    getBtnIndex(index) {
      this.$emit('getBtnIndex', index)
    }
  },
}
</script>


<style lang="scss">
@mixin media-size($size){
  @media(max-width: $size){
    @content
  }
}

.nav-about{
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  align-items: flex-start;
  margin: 60px 0;
  // Media
  @include media-size(1020px){
    flex-wrap: wrap;
    justify-content: space-evenly;
  }
  
  .btn-box{
    width: 380px;
    // Media
    @include media-size(1700px){
      width: 320px;
    }
    @include media-size(1430px){
      width: 280px;
    }
    @include media-size(1230px){
      width: 230px;
    }
    @include media-size(1020px){
      width: 40%;
    }
    @include media-size(570px){
      width: 100%;
    }
    
    &:hover button{
      transform: translateY(-5px);
    }
    
    button{
      width: 100%;
      height: 100%;
      min-height: 160px;
      padding: 50px;
      font-size: 25px;
      font-weight: 600;
      font-family: 'Montserrat';
      background: #FFFFFF;
      border: none;
      border-radius: 10px;
      box-shadow: 0 0 30px rgba(34, 104, 203, 0.4);
      cursor: pointer;
      transition: 0.3s;
      // Media
      @include media-size(1700px){
        font-size: 22px;
      }
      @include media-size(1430px){
        padding: 40px;
        min-height: 135px;
      }
      @include media-size(1230px){
        font-size: 18px;
        padding: 20px;
        min-height: 100px;
      }
      @include media-size(1020px){
        min-height: 70px;
        margin: 15px 0;
      }
      @include media-size(790px){
        font-size: 16px;
        min-height: 80px;
        margin: 15px 0;
      }
      @include media-size(570px){
        font-size: 15px;
        min-height: 70px;
        margin: 10px 0;
      }
    }
    
    &.active button{
      color: #FFFFFF;
      background: #2268c8;
    }
  }
}
</style>